// stylelint-disable scss/dollar-variable-pattern
// stylelint-disable meowtec/no-px
// stylelint-disable scale-unlimited/declaration-strict-value
// stylelint-disable selector-max-class
// stylelint-disable scss/double-slash-comment-whitespace-inside
// stylelint-disable color-named
// stylelint-disable declaration-no-important
// stylelint-disable max-nesting-depth
// stylelint-disable scss/selector-no-redundant-nesting-selector
// stylelint-disable selector-max-compound-selectors

$treePrefixCls: 'rc-tree';
$treeNodePrefixCls: '#{$treePrefixCls}-treenode';

.#{$treePrefixCls} {
	border: 1px solid transparent;
	margin: 0;

	&-focused:not(&-active-focused) {
		border-color: tint-color($info, 50%);
	}

	// padding: 5px;
	.#{$treeNodePrefixCls} {
		padding: 0;
		margin: 0;
		line-height: 24px;
		list-style: none;
		outline: 0;
		white-space: nowrap;

		.draggable {
			color: #333;
			user-select: none;

			/* Required to make elements draggable in old WebKit */
			// -khtml-user-drag: element;
			// -webkit-user-drag: element;
		}

		&.dragging {
			background: rgba(100, 100, 255, 0.1);
		}

		&.drop-container {
			> .draggable::after {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				box-shadow: inset 0 0 0 2px red;
				content: '';
			}
			& ~ .#{$treeNodePrefixCls} {
				border-left: 2px solid chocolate;
			}
		}

		&.drop-target {
			background-color: $success;
			& ~ .#{$treeNodePrefixCls} {
				border-left: none;
			}
		}

		&.filter-node {
			> .#{$treePrefixCls}-node-content-wrapper {
				color: $danger !important;
				font-weight: 700 !important;
			}
		}

		ul {
			padding: 0 0 0 18px;
			margin: 0;
		}
		.#{$treePrefixCls}-node-content-wrapper {
			position: relative;
			display: inline-block;
			height: 24px;
			padding: 0;
			margin: 0;
			cursor: pointer;
			text-decoration: none;
			vertical-align: top;
		}

		span {
			&.#{$treePrefixCls}-switcher,
			&.#{$treePrefixCls}-checkbox,
			&.#{$treePrefixCls}-iconEle {
				display: inline-block;
				width: 16px;
				height: 16px;
				border: 0 none;
				margin-right: 2px;
				background-attachment: scroll;
				background-color: transparent;
				background-image: url('data:image/png;base64,');
				background-repeat: no-repeat;
				cursor: pointer;
				line-height: 16px;
				outline: none;
				vertical-align: sub;

				&.#{$treePrefixCls}-icon__customize {
					background-image: none;
				}
			}
			&.#{$treePrefixCls}-icon_loading {
				margin-right: 2px;
				background: url('data:image/gif;base64,')
					no-repeat scroll 0 0 transparent;
				vertical-align: top;
			}
			&.#{$treePrefixCls}-switcher {
				&.#{$treePrefixCls}-switcher-noop {
					cursor: auto;
				}
				&.#{$treePrefixCls}-switcher_open {
					background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="rgb(#{hex-to-rgb($dark)})" style="font-size: 1rem;"><path d="M0 0h24v24H0V0z" fill="none"></path><path d="M7.41 8.59L12 13.17l4.59-4.58L18 10l-6 6-6-6 1.41-1.41z"></path></svg>') !important;
				}
				&.#{$treePrefixCls}-switcher_close {
					background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="rgb(#{hex-to-rgb($dark)})" style="font-size: 1rem;"><path d="M0 0h24v24H0V0z" fill="none"></path><path d="M8.59 16.59L13.17 12 8.59 7.41 10 6l6 6-6 6-1.41-1.41z"></path></svg>') !important;
				}
			}
			&.#{$treePrefixCls}-checkbox {
				width: 16px;
				height: 16px;
				margin: 0 3px;
				background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="rgb(#{hex-to-rgb($dark)})" style="font-size: 1rem;"><path d="M0 0h24v24H0V0z" fill="none"></path><path d="M19 5v14H5V5h14m0-2H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2z"></path></svg>') !important;

				&-checked {
					background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="rgb(#{hex-to-rgb($success)})" style="font-size: 1rem;"><path d="M0 0h24v24H0V0z" fill="none"></path><path d="M5 19h14V5H5v14zm2.41-7.4l2.58 2.58 6.59-6.59L17.99 9l-8 8L6 13.01l1.41-1.41z" opacity="0.3"></path><path d="M19 3H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zm0 16H5V5h14v14zM17.99 9l-1.41-1.42-6.59 6.59-2.58-2.57-1.42 1.41 4 3.99z"></path></svg>') !important;
				}

				&-indeterminate {
					background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="rgb(#{hex-to-rgb($info)})" style="font-size: 1rem;"><path d="M0 0h24v24H0z" fill="none"></path><path d="M5 19h14V5H5v14zm2-8h10v2H7v-2z" opacity="0.3"></path><path d="M19 3H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zm0 16H5V5h14v14zM7 11h10v2H7z"></path></svg>') !important;
				}

				&-disabled {
					background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="rgb(#{hex-to-rgb($dark)})" style="font-size: 1rem;"><path d="M0 0h24v24H0V0z" fill="none"></path><path d="M19 5v14H5V5h14m0-2H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2z"></path></svg>') !important;
					opacity: 0.25;

					& ~ * {
						opacity: 0.25;
					}
				}
				&.#{$treePrefixCls}-checkbox-checked.#{$treePrefixCls}-checkbox-disabled {
					background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="rgb(#{hex-to-rgb($dark)})" style="font-size: 1rem;"><path d="M0 0h24v24H0V0z" fill="none"></path><path d="M5 19h14V5H5v14zm2.41-7.4l2.58 2.58 6.59-6.59L17.99 9l-8 8L6 13.01l1.41-1.41z" opacity="0.3"></path><path d="M19 3H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zm0 16H5V5h14v14zM17.99 9l-1.41-1.42-6.59 6.59-2.58-2.57-1.42 1.41 4 3.99z"></path></svg>') !important;
				}
				&.#{$treePrefixCls}-checkbox-indeterminate.#{$treePrefixCls}-checkbox-disabled {
					position: relative;
					background: #ccc;
					border-radius: 3px;

					&::after {
						position: absolute;
						top: 5px;
						left: 3px;
						width: 5px;
						height: 0;
						border: 2px solid #fff;
						border-top: 0;
						border-left: 0;
						content: ' ';
						transform: scale(1);
					}
				}
			}
		}
	}
	&:not(.#{$treePrefixCls}-show-line) {
		.#{$treeNodePrefixCls} {
			.#{$treePrefixCls}-switcher-noop {
				background: none;
			}
		}
	}
	&.#{$treePrefixCls}-show-line {
		.#{$treeNodePrefixCls}:not(:last-child) {
			> ul {
				background: url('data:image/gif;base64,R0lGODlhCQACAIAAAMzMzP///yH5BAEAAAEALAAAAAAJAAIAAAIEjI9pUAA7')
					0 0 repeat-y;
			}
			> .#{$treePrefixCls}-switcher-noop {
				background-position: -56px -18px;
			}
		}
		.#{$treeNodePrefixCls}:last-child {
			> .#{$treePrefixCls}-switcher-noop {
				background-position: -56px -36px;
			}
		}
	}

	/*  */
	&-child-tree {
		display: none;

		&-open {
			display: block;
		}
	}

	/*  */
	&-treenode-disabled {
		> span:not(.#{$treePrefixCls}-switcher),
		> a,
		> a span {
			cursor: not-allowed;
			opacity: 0.5;
		}
	}

	/*  */
	&-treenode-active {
		background: rgba(0, 0, 0, 0.1);

		// .#{$treePrefixCls}-node-content-wrapper {
		//   background: rgba(0, 0, 0, 0.1);
		// }
	}

	/*  */
	&-node-selected {
		background-color: tint-color($warning, 50%);
		border-radius: 0.5rem;
		box-shadow: 0 0 0 1px shade-color($warning, 10%);
		opacity: 0.8;
	}

	/* OPEN FOLDER */
	&-icon__open {
		margin-right: 2px;
		background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="rgb(#{hex-to-rgb($warning)})" style="font-size: 1rem;"><path d="M0 0h24v24H0V0z" fill="none"></path><path d="M4 8h16v10H4z" opacity="0.3"></path><path d="M20 6h-8l-2-2H4c-1.1 0-1.99.9-1.99 2L2 18c0 1.1.9 2 2 2h16c1.1 0 2-.9 2-2V8c0-1.1-.9-2-2-2zm0 12H4V8h16v10z"></path></svg>') !important;
	}

	/* CLOSE FOLDER */
	&-icon__close {
		margin-right: 2px;
		background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="rgb(#{hex-to-rgb(shade-color($warning,20%))})" style="font-size: 1rem;"><path d="M0 0h24v24H0V0z" fill="none"></path><path d="M11.17 8l-.58-.59L9.17 6H4v12h16V8h-8z" opacity="0.3"></path><path d="M20 6h-8l-2-2H4c-1.1 0-1.99.9-1.99 2L2 18c0 1.1.9 2 2 2h16c1.1 0 2-.9 2-2V8c0-1.1-.9-2-2-2zm0 12H4V6h5.17l1.41 1.41.59.59H20v10z"></path></svg>') !important;
	}

	/* ICON DOC */
	&-icon__docu {
		margin-right: 2px;
		background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="rgb(#{hex-to-rgb($dark)})" style="font-size: 1rem;"><path d="M0 0h24v24H0V0z" fill="none"></path><path d="M13 4H6v16h12V9h-5z" opacity="0.3"></path><path d="M20 8l-6-6H6c-1.1 0-1.99.9-1.99 2L4 20c0 1.1.89 2 1.99 2H18c1.1 0 2-.9 2-2V8zm-2 12H6V4h7v5h5v11z"></path></svg>') !important;
	}

	&-icon__customize {
		margin-right: 2px;
		vertical-align: top;
	}

	&-title {
		display: inline-block;
	}

	&-indent {
		display: inline-block;
		height: 0;
		vertical-align: bottom;
	}

	&-indent-unit {
		display: inline-block;
		width: 16px;
	}

	&-draggable-icon {
		display: inline-flex;
		width: 16px;
		justify-content: center;
	}
}

// stylelint-enable scss/dollar-variable-pattern
// stylelint-enable meowtec/no-px
// stylelint-enable scale-unlimited/declaration-strict-value
// stylelint-enable selector-max-class
// stylelint-enable scss/double-slash-comment-whitespace-inside
// stylelint-enable color-named
// stylelint-enable declaration-no-important
// stylelint-enable max-nesting-depth
// stylelint-enable scss/selector-no-redundant-nesting-selector
// stylelint-enable selector-max-compound-selectors
